import React from 'react';
import Loader from '../../components/loader/Loader';
import IntershipStyles from './Intership.style';

const Intership = (props) => {
    const {
        theme,
        data
    } = props;
    console.log(data)
    if(!data) {
        return null;
    }

    return (
        <IntershipStyles>
            <div className="container-slides">
                <div className="wrap-iframe d-flex align-items-center pos-relative">
                    <iframe src={data.link_docs.intership} frameBorder="0" width="100%" height="100%" allowFullScreen={true}></iframe>
                    <Loader color={theme.primary} size={8} />
                </div>
            </div>
        </IntershipStyles>
    )
}

export default Intership;